			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">9.
								您发布的内容</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">9.1
								所有权：【麦咚陪诊】对通过产品和服务提供或在其上显示的任何提交、张贴、传输的用户内容，包括但不限于网站、软件或产品和服务中的数据、文本、声音、图像、视频、图表、软件、代码、音频、信息、图形或说明（&quot;用户内容&quot;），不主张所有权。【麦咚陪诊】对用户内容不提供报酬，除非另有书面约定。【麦咚陪诊】不承担张贴或使用任何用户内容的义务。除非与【麦咚陪诊】授权的代表另有书面协议，您提交或张贴的任何材料、信息或其他通信均被认作非机密和非专属。</span></span></span></span>
