							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">方服务。如果在您使用第三方服务的过程中产生任何事故或问题的，由您与第三方自行协商解决，【</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">麦咚陪诊】对此不承担任何责任。</span></span></span></span></span></p>

