			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.4.3
								【麦咚陪诊】根据自己的独立判断认为您对产品和服务的使用行为不符【麦咚陪诊】要求或用户需求。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.5
								【麦咚陪诊】的终止：尽管有上述规定，【麦咚陪诊】保留在不事先通知您的情况下随时变更、中断或终止提供全部或部分产品和服务的权利，除非特定产品和服务条款另有规定。不论何种原因，对于因【麦咚陪诊】对产品和服务的变更、中断或终止而造成的任何损失或责任，由您自行独立承担，【麦咚陪诊】无需因此对您或任何第三方承担任何责任。</span></span></span></span>
			</p>

