							style="font-size:12.0000pt"><span style="font-family:等线">18.1
								其他地区的访问：【麦咚陪诊】不承诺产品和服务对中国以外的其他地区适用或可用；并且禁止在其内容被认为是非法的地区对其访问。从其他区域访问产品和服务的人士均为自愿所为，并对遵守本地法律负全部责任。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.2
								可分割性：如本条款中的任何条款无论因何种原因无效或不具有执行力，则该条款应在不影响其他条款的情况下从本条款中被移除。本条款的其余条款仍应有效并且具有法律约束力。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.3
								您的违反：如果出现您对这些规则和规定的违反行为，【麦咚陪诊】保留针对该违反行为采取法律所能提供的所有补救手段的权利。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.4
								无其他权利：【麦咚陪诊】保留本条款未明确授予您的所有权利，并且不能将本条款理解为以默示或其他方式授予您任何其他权利。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.5
								语言：本条款的英文翻译版本仅为您的理解便利而提供，【麦咚陪诊】和您之间的关系由本条款的中文版确定。如果两个版本存在任何冲突之处，则以中文版本规定为准。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.6
								转让：为了【麦咚陪诊】及其继承人和受让人的利益，本条款项下全部权利可以被【麦咚陪诊】转让。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.7
								控制权变更：如果您是一个公司或其他组织的情况下，在您的控制权变更的情况下，您的资产和业务的承继方应自动受到本条款的约束。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.8
								客服：您对产品和服务或本条款的全部或任何部分的意见及建议可通过相关网站或产品进行反馈，或拨打电话</span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">客服电话</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">呼叫客服与【麦咚陪诊】进行联系。</span></span></span></span></span></p>

			<p style="text-align:justify">&nbsp;</p>

			<p style="text-align:justify">&nbsp;</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">用户授权协议</span></span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span></span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1.
								接受条款</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.1.
								欢迎光临【麦咚陪诊】。请在使用【麦咚陪诊】相关服务之前仔细阅读（未成年人应在法定监护人陪同下阅读）本服务条款（&quot;本条款&quot;）。您对【麦咚陪诊】相关服务的访问和使用表明您认可并同意遵守本条款。如果您不同意本条款，请不要使用相关服务或下载任何材料。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.2.
								您声明您有充分的权力、能力和权限来接受本条款。如果您代表您的雇主或另一机构实体接受本条款和通用服务条款，您声明您必须有充分的法律授权使您的雇主或机构实体受到本条款的约束。如果您没有充分的法律授权，请您确保由您的机构实体的授权人士来同意本条款。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.3.
								本条款适用于【麦咚陪诊】软件有限公司及其附属公司和关联公司（&quot;【麦咚陪诊】&quot;）的所有网站（以下均称为&quot;网站&quot;）以及【麦咚陪诊】通过互联网提供的各种产品（以下均称为&quot;产品&quot;）。【麦咚陪诊】允许您访问和使用网站和产品上的各种资源，包括文档、照片、视频、地图数据及其他图形、文字或影音内容（以下简称&quot;内容&quot;），软件，以及交流论坛和其他服务（与内容、软件合称为&quot;服务&quot;）。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.4.
								【麦咚陪诊】保留随时更新本条款的权利，恕不另行通知。请您注意查看网站底部的&ldquo;服务条款&rdquo;阅读，获取服务条款的最新适用版本。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.5.
								【麦咚陪诊】可能会提供针对具体产品、服务、活动或项目的服务条款。在此类情况下，如果这本条款与具体服务条款不相冲突，则此处的条款保持完整有效。如果您不能接受修订，您可以停止使用【麦咚陪诊】产品或服务。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">1.6.
								除非另有明确规定，【麦咚陪诊】所推出的新网站和新产品，均受到本条款的规范。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">2.
								安全和隐私</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">2.1 个人信息：《【麦咚陪诊】隐私权政策》
								介绍了您在使用【麦咚陪诊】服务时，我们会如何处理您的个人信息和保护您的隐私。您对服务的使用即表示您同意我们可以按照我们的隐私权政策使用您的个人信息。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">2.2 帐户、密码和安全性：
								如果任何【麦咚陪诊】服务要求您开设帐户，您必须根据相应注册界面上的说明，向我们提供最新的、完整的、准确的相关信息，以便完成注册过程。您必须对维护密码和帐户的机密性承担全部责任。此外，您需要对帐户中发生的所有活动承担全部责任。您同意，一旦发现帐户存在未经授权的使用或任何其他不安全的行为，马上向【麦咚陪诊】通知相关情况。如果其他人在您知情或不知情的情况下使用您的密码或帐户，【麦咚陪诊】对您因此可能蒙受的任何损失不承担任何责任。不过，如【麦咚陪诊】或第三方因其他人使用您的帐户</span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">或密码而蒙受损失，</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">【麦咚陪诊】</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">应承担相应责任。未经帐户持有人的同意，您不得使用其他人的帐户。关于用户帐户的具体规则，请参见《【</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">麦咚陪诊】用户注册协议》</span> <span
									style="font-family:等线">。</span></span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3. 使用规则</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.1
								个人非商业用途：产品和服务专用于个人和非商业用途。除非在条款中提及或指定，您不得对从产品服务中获得的内容进行修改、复制、发布、传输、展示、播放、翻制、出版、授权、从其创建衍生产品、转让或出售。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2 不可用于非法或被禁止的用途：
								您同意不将产品和服务用于非法的或者本条款禁止的用途。您不可以：</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.1
								违反《中华人民共和国电信条例》的规定，利用产品和服务实施危害国家主权、国家安全、祖国统一、社会稳定、公序良俗的行为，或任何不当的、侮辱诽谤的、淫秽的、暴力的行为及任何违反其他国家法律、法规、规章、政策等的行为；</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.2
								利用产品和服务实施侵害他人知识产权、商业秘密权等合法权益的行为；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.3
								利用产品和服务批量发表、传送、传播广告信息、垃圾信息及其他违法信息；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.4
								利用产品和服务进行任何可能对互联网或其他通信网络的正常运转造成不利影响的行为；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.5
								利用产品和服务制作、发布、传播用于窃取帐号及他人专属信息、财产、保密信息的软件；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.6
								使用产品和服务&quot;跟踪&quot;，或骚扰或伤害他人；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.7
								假冒他人或团体，包括，但不限于，【麦咚陪诊】负责人、论坛坛主、导游或主持人；或者谎称或假称您与某个人或组织的关系；或者收集或存储有关其他用户的个人数据用于被禁止的行为或活动;</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.8
								伪造标题或以其他方式篡改识别资料，以混淆通过产品和服务传达的原内容；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.9
								试图通过黑客活动、密码挖掘或其他任何手段未经许可访问产品和服务，或访问与任何【麦咚陪诊】服务器或服务连接的其他帐户、计算机系统或网络；</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.10
								通过产品和服务无意提供的方法获取或试图获取任何材料或信息；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.11
								进行任何不利于【麦咚陪诊】的行为；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.2.12
								其他以任何不合法的方式、为任何不合法的目的、或以任何与本条款不一致的方式使用产品和服务。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.3
								通信服务：服务可能包括公告板服务、聊天区、论坛、社区和/或其他消息或通信设施，方便您与其他人进行通信（以下简称&quot;通信产品和服务&quot;）。您同意仅使用通信服务张贴和收发恰当的、与特定通信服务相关（如果适用）的消息和资料。【麦咚陪诊】保留查看张贴到通信服务上的资料以及自行决定移除任何资料的权利。【麦咚陪诊】保留根据任何适用法律、法规、法律程序或政府要求、在【麦咚陪诊】认为必要的情况下随时披露任何信息的权利。【麦咚陪诊】保留自行决定编辑、拒绝张贴或移除任何信息、资料（全部或部分内容）的权利。在任何通信</span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">服务中提供任何有关您自己或孩子的个人识别信息和商业敏感信息时，请始终谨慎行事。【</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">麦咚陪诊】不会控制或认可任何通信服务中出现的内容、消息或信息，且【麦咚陪诊】明确声明对通信服务不承担任何责任。上传到通信服务的资料可能受到有关使用、复制或散布的公开限制。如果您下载资料，应遵守这些限制。</span></span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">3.4
								接入互联网：【麦咚陪诊】仅提供产品和服务，除此之外与产品和服务使用有关的任何设备（如产品和服务器、电脑、调制解调器及其他与接入互联网有关的装置）及所需的费用（如为接入互联网而支付的电话费及上网费）均应由您自行负担。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">4.
								免责、担保与保证</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">4.1
								担保的排除：产品和服务由【麦咚陪诊】提供，作为对其客户的一项服务。您完全了解并同意：</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">4.1.1
								产品和服务同大多数互联网应用程序一样，受包括但不限于您的原因、网络产品和服务质量、社会环境等因素的差异影响，可能受到各种安全问题的侵扰，例如您下载安装的其它软件或访问的其他网站中含有&quot;特洛伊木马&quot;等病毒，威胁到您的设备和数据的安全，继而影响产品和服务的正常使用等。您应加强信息安全及资料的保护意识，以免遭致损失。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">4.1.2
