			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">9.3
								调整：您认可【麦咚陪诊】有可能会对用户内容进行调整。【麦咚陪诊】及其指定人员保留作为【麦咚陪诊】单方决定预筛、拒收、或者从其网站或产品和服务移除任何用户内容的权利。但是【麦咚陪诊】不承担任何与任何用户内容有关的责任，不论其是否牵涉著作权、诽谤、隐私、淫秽或者其他方面的法律。不限于此，【麦咚陪诊】将保留移除任何违反产品和服务条款的用户内容的权利。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">10.
								您的权利保护</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">10.1
								若您合理地认为第三方利用产品和服务侵犯您的知识产权或其他合法权益，请根据【麦咚陪诊】的要求提供支持性资料（包括但不限于您对涉嫌侵权内容的权属证明，关于侵权的具体情况的说明，涉嫌侵权内容在网络上的位置，您的联络信息、身份证明、单位登记证明），【麦咚陪诊】将会依照法律法规的要求进行处理。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">10.2
								为确保【麦咚陪诊】能够有效处理相关权利请求，请您在权利通知中加入如下真实性声明：
								&quot;我声明并保证，本通知中所述信息是充分、真实、准确的，如果本权利通知内容不完全属实，本人将承担由此产生的一切法律责任。&quot;如果您是个人，请您签署该文件；如果您是依法成立的机构或组织，请您加盖公章。　</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">10.3
								如果权利通知的陈述失实，您将承担由此造成的全部法律责任（包括但不限于赔偿各种费用及律师费）。如果您对该侵权行为并无确信，建议您首先咨询专业人士。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">10.4
								请您把上述资料书面发往以下地址：中国北京市朝阳区望京阜通东大街方恒国际中心A座16层。　</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">11.
								其他资源或网站的链接第三方服务</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">11.1
								第三方服务链接：网站、产品和服务可能会以外部链接、服务接口等任何形式指向包含指向其他第三方服务、平台、网站、内容、资源等（统称为&quot;第三方服务&quot;）的外部链接。这些第三方服务网站、内容及资源均由第三方其他公司或个人提供，【麦咚陪诊】对其无控制权。您承认并同意，【麦咚陪诊】对第三方服务这些外部的网站或资源的可用性、真实性或有效性不提供任何形式的保证，也不承担任何责任。在您使用第三方服务时，请您遵守第三方要求您遵守的服务条款，许可协议或其他类似法律文件。为了您的人身和财产安全，请您谨慎判断是否使用第三</span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">方服务。如果在您使用第三方服务的过程中产生任何事故或问题的，由您与第三方自行协商解决，【</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">麦咚陪诊】对此不承担任何责任。</span></span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">12.
								与广告商进行之交易</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">12.1
								广告交易：您通过网站、产品和服务与广告商进行任何形式的通讯或商业往来，完全为您与广告商之间之行为。有关法律法规有明文规定要求【麦咚陪诊】承担责任以外，您因前述任何交易或前述广告商而遭受的任何性质的损失或损害，【麦咚陪诊】均不予负责。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">13.
								接受系统邮件、消息</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">13.1
								邮件与消息：如果您使用产品和服务，即表示您愿意接受产品和服务发出的相关邮件或【麦咚陪诊】系统消息（包括但不限于商品促销等广告信息）。因此而有可能给您带来的不便，【麦咚陪诊】不负担任何责任。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.
								变更、中断或终止</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.1
								变更：【麦咚陪诊】保留在发出通知或不发出通知的情况下对产品和服务进行临时的或永久的修改或停止提供的权利。【麦咚陪诊】可以在不发出通知的情况下随时对产品和服务进行更改。【麦咚陪诊】不对更新网站/产品和服务做任何承诺。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.2
								中断：如因【麦咚陪诊】相关服务器或系统维护或升级的需要而需中断或暂停提供任何产品和服务的，【麦咚陪诊】将尽可能事先进行通告。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.3
								您的终止：您可随时单方终止使用产品和服务，您终止使用无需特别通知【麦咚陪诊】。您主动终止使用产品和服务时，本条款终止。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.4
								您应确保您对产品和服务的使用行为符合本条款的约定、符合国家相关法律法规的要求、符合人们基本的道德标准。如发生下列任何一种情形或本条款其他条款规定的其他终止条件发生或实现的，【麦咚陪诊】有权随时中断或终止向您提供产品和服务：</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.4.1
								您提供的个人资料或其他资料不真实、虚假或违法；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.4.2
								您对产品和服务的使用行为违反国家法律法规或本条款的任何规定；</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.4.3
								【麦咚陪诊】根据自己的独立判断认为您对产品和服务的使用行为不符【麦咚陪诊】要求或用户需求。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.5
								【麦咚陪诊】的终止：尽管有上述规定，【麦咚陪诊】保留在不事先通知您的情况下随时变更、中断或终止提供全部或部分产品和服务的权利，除非特定产品和服务条款另有规定。不论何种原因，对于因【麦咚陪诊】对产品和服务的变更、中断或终止而造成的任何损失或责任，由您自行独立承担，【麦咚陪诊】无需因此对您或任何第三方承担任何责任。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.6
								产品和服务由于任何原因终止时，您根据本条款所获得的一切权利或许可全部终止。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">14.7
								有效条款：无论产品和服务是否终止，关于知识产权保护、隐私保护、赔偿责任、免责及责任限制、法律适用和争议解决等条款始终有效。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">15.
								法律适用和争议解决</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">15.1
								适用法：用户对产品和服务的访问受相关中国法律法规的制约，而不考虑其冲突法的规定，并可能同时受您居住国强制性法律的制约。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">15.2
								国际使用之特别警告：您已了解国际互联网的无国界性，同意遵守当地所有关于网上行为及内容之法律法规。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">15.3
								争议解决：如果发生任何与本条款的解释或执行有关的争议，双方应尝试通过友好协商解决争议。如果争议在90日内不能以双方接受的方式解决，双方同意将争议提交至【麦咚陪诊】软件有限公司住所地法院解决。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">16. 通知和送达 / NOTICES AND
								SERVICE</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">16.1
								您在此同意，【麦咚陪诊】关于产品和服务、本条款内容变化的所有通知均可通过产品和服务界面、网页公告、电子邮件、短信提醒或常规信件传送等方式进行。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">16.2
								上述通知于发送之日即视为已送达收件人（即您）。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">17.
								青少年用户特别提示</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">17.1
								特别提示：青少年用户必须遵守全国青少年网络文明公约：要善于网上学习，不浏览不良信息；要诚实友好交流，不侮辱欺诈他人；要增强自护意识，不随意约会网友；要维护网络安全，不破坏网络秩序；要有益身心健康，不沉溺虚拟时空。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.
								其他规定</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.1
								其他地区的访问：【麦咚陪诊】不承诺产品和服务对中国以外的其他地区适用或可用；并且禁止在其内容被认为是非法的地区对其访问。从其他区域访问产品和服务的人士均为自愿所为，并对遵守本地法律负全部责任。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.2
								可分割性：如本条款中的任何条款无论因何种原因无效或不具有执行力，则该条款应在不影响其他条款的情况下从本条款中被移除。本条款的其余条款仍应有效并且具有法律约束力。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.3
								您的违反：如果出现您对这些规则和规定的违反行为，【麦咚陪诊】保留针对该违反行为采取法律所能提供的所有补救手段的权利。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.4
								无其他权利：【麦咚陪诊】保留本条款未明确授予您的所有权利，并且不能将本条款理解为以默示或其他方式授予您任何其他权利。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.5
								语言：本条款的英文翻译版本仅为您的理解便利而提供，【麦咚陪诊】和您之间的关系由本条款的中文版确定。如果两个版本存在任何冲突之处，则以中文版本规定为准。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.6
								转让：为了【麦咚陪诊】及其继承人和受让人的利益，本条款项下全部权利可以被【麦咚陪诊】转让。</span></span></span></span></p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.7
								控制权变更：如果您是一个公司或其他组织的情况下，在您的控制权变更的情况下，您的资产和业务的承继方应自动受到本条款的约束。</span></span></span></span>
			</p>

			<p style="text-align:justify"><span style="font-size:10.5pt"><span style="font-family:等线"><span
							style="font-size:12.0000pt"><span style="font-family:等线">18.8
								客服：您对产品和服务或本条款的全部或任何部分的意见及建议可通过相关网站或产品进行反馈，或拨打电话</span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">客服电话</span></span></span><span
							style="font-size:12.0000pt"><span style="font-family:等线"><span
									style="font-family:等线">呼叫客服与【麦咚陪诊】进行联系。</span></span></span></span></span></p>
		</view>

	</view>
</template>

